import React from 'react'
import { Button, Card, Container, Form, Row } from 'react-bootstrap'
import { useDispatch, useSelector } from 'react-redux'
import { message } from 'antd'
import axios from 'axios'
import { setUser } from '../../features/userSlice'

const Index = () => {
  const user = useSelector((state) => state.user.user)
  const [uniqueId, setUniqueId] = React.useState('')
  const [amount, setAmount] = React.useState('')
  const dispatch = useDispatch()
  const handleTransfer = async (e) => {
    try {
      e.preventDefault()
      e.stopPropagation()

      if (!uniqueId || !amount) {
        return message.error('Please fill all the fields')
      }
      if (amount < 0) {
        return message.error('Amount cannot be negative')
      }
      if (user.walletBalance < amount) {
        return message.error('Insufficient Balance')
      }
      if (user.uniqueId === uniqueId) {
        return message.error('Cannot transfer to same user')
      }
      const data = {
        uniqueId,
        amount,
        senderId: user.uniqueId,
      }
      const res = await axios.post('api/v1/user/transfertoUser', data)
      if (res.data.success) {
        message.success(res.data.message)
        dispatch(setUser({ ...user, walletBalance: user.walletBalance - amount }))
        setUniqueId('')
        setAmount('')
      } else {
        message.error(res.data.message)
      }
    } catch (error) {
      message.error(error.response.data.message)
    }
  }
  return (
    <>
      <Container>
        <Row className="text-danger">
          <h3>*Note: If transfered to other user wallet, amount cannot be retrived</h3>
        </Row>
      </Container>
      <Container>
        <Card>
          <Card.Header className="mb-3">Transfer Wallet Amount</Card.Header>
          <div className="col-12">
            <Form noValidate className="ms-3" onSubmit={handleTransfer}>
              <Form.Group className="col-12 col-md-4 mb-3">
                <Form.Label>UniqueId</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="Enter UniqueId"
                  required
                  value={uniqueId}
                  onChange={(e) => setUniqueId(e.target.value)}
                />
              </Form.Group>

              <Form.Group className="col-12 col-md-4 mb-3">
                <Form.Label>Amount</Form.Label>
                <Form.Control
                  type="number"
                  placeholder="Enter Amount"
                  required
                  value={amount}
                  onChange={(e) => setAmount(e.target.value)}
                />
              </Form.Group>
              <Button type="submit" className="myCustomBtn mb-3">
                Add
              </Button>
            </Form>
          </div>
        </Card>
      </Container>
    </>
  )
}

export default Index
